<template>
  <div class="index">
    <div class="login">
      <label class="item">
        <span class="item-span">用户名：</span>
        <input type="text" class="item-input" v-model="userId">
      </label>
      <label class="item">
        <span class="item-span">密码：</span>
        <input type="password" class="item-input" v-model="pwd">
      </label>
      <button class="btn" @click="login">登录</button>
    </div>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        userId:'9527',
        pwd:'admin'
      }
    },
    methods:{
      login(){
        let userId=this.userId;
        let pwd=this.pwd;
        let params={userId,pwd};
        this.$axios.post("login",params).then(res=>{
          if (res.code == 200) {
            this.$message({
              message: '登录成功',
              type: 'success'
            });
            this.$router.push('/news')
          } else {
            this.$message.error('登录失败');
          }
        });
      }
    }
  }
</script>

<style scoped>
  .index {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: url("../../static/images/sky05.jpg");
    background-size: 100% 100%;
  }

  .login {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 400px;
    height: 200px;
    background: url("../../static/images/sky02.jpg");
    background-size: 100% 100%;
  }

  .item {
    display: block;
    width: 70%;
    height: 30px;
    margin: 30px auto 0;
    color: #fff;
    font-size: 16px;
  }

  .btn {
    display: block;
    line-height: 30px;
    white-space: nowrap;
    cursor: pointer;
    color: #fff;
    background-color: #67c23a;
    border: 1px solid #67c23a;
    text-align: center;
    box-sizing: border-box;
    outline: none;
    margin: 30px auto 0;
    font-weight: 500;
    width: 100px;
    height: 30px;
    font-size: 15px;
  }

  .item-span {
    display: inline-block;
    width: 80px;
    height: 30px;
    line-height: 30px;
    text-align: right;
  }
  .item-input{
    height: 30px;
    line-height: 30px;
    border: none;
    opacity: 0.8;
  }
</style>



